<template>
  <div>
    <h1>订单列表</h1>
    <el-input
      type="text"
      style="width: 200px;"
      placeholder="请输入要查询的账户名称"
      v-model="account.account"
      @keyup.enter.native="searchAccount"
    ></el-input>
    <el-button type="primary" size="small" style="margin-left: 1rem;" @click="searchAccount">根据账号查询</el-button>
    <el-table :data="borrow.brrowinfos">
      <el-table-column prop="broId" label="订单号" width="120"></el-table-column>
      <el-table-column prop="account.account" label="用户" width="120"></el-table-column>
      <el-table-column prop="book.bookName" label="书籍" width="120"></el-table-column>
      <el-table-column prop="book.bookPhoto" label="图片" width="160">
        <template slot-scope="scope">
          <img
            style="width:60px;height: 60px;"
            :src="`http://localhost:8080${scope.row.book.bookPhoto}`"
          />
        </template>
      </el-table-column>
      <el-table-column prop="lendtime" :formatter="dateFormat" label="借阅时间" width="120"></el-table-column>
      <el-table-column prop="returntime" :formatter="dateFormat" label="归还时间" width="120"></el-table-column>
      <el-table-column prop="applytime" label="申请时间" width="120"></el-table-column>
      <el-table-column prop="status" :formatter="statusFormat" label="状态" width="120"></el-table-column>
    </el-table>
  </div>
</template>

<script>
import Axios from "axios";
import moment from "moment";

export default {
  data() {
    return {
      borrow: {},
      account: {},
    };
  },
  methods: {
    //获取数据
    fetch() {
      var that = this;
      Axios.post("http://localhost:8080/Manager/getBrrowInfo").then(function(
        response
      ) {
        that.borrow = response.data.map;
      });
    },
    //时间格式转化
    dateFormat: function(row, column) {
      var date = row[column.property];
      if (date == undefined) {
        return "";
      }
      return moment(date).format("YYYY-MM-DD");
    },
    //状态格式转化
    statusFormat(row, column) {
      var status = row[column.property];
      if (status == 0) {
        return "未提交"
      } else {
        return "已提交"
      }
    },
    //查询账户订单
    searchAccount() {
      if (this.account.account == "") {
        this.$router.go(0);
      } else {
        var that = this;
        Axios.post(
          "http://localhost:8080/Manager/getBrrowInfoByAccount",
          this.$qs.stringify(that.account)
        ).then(function(response) {
          console.log(response)
          if (response.data.code == 100) {
            that.borrow.brrowinfos = response.data.map.brrowinfobyaccount;
            that.borrow.pageSize = 100;
            // that.model.total = response.data.map.brrowinfobyaccount.length;
          } else {
            that.$message({
              type: "error",
              message: response.data.map.err_msg
            });
          }
        });
      }
    },
  },
  created() {
    this.fetch();
  }
};
</script>