<template>
  <el-container style="height: 100vh;">
    <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
      <el-menu router :default-openeds="['1']">
        <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-message"></i>管理员
          </template>
          <el-menu-item-group>
            <template slot="title">书籍管理</template>
            <el-menu-item index="/book/edit">添加书籍</el-menu-item>
            <el-menu-item index="/book/list">书籍列表</el-menu-item>
          </el-menu-item-group>
          <el-menu-item-group>
            <template slot="title">订单管理</template>
            <el-menu-item index="/borrow/list">订单列表</el-menu-item>
          </el-menu-item-group>
        </el-submenu>
      </el-menu>
    </el-aside>

    <el-container>
      <el-header style="text-align: right; font-size: 12px">
        <el-dropdown>
          <i class="el-icon-setting" style="margin-right: 15px"></i>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item @click.native="logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <span>欢迎登陆图书管理系统</span>
      </el-header>

      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<style>
  .el-header {
    background-color: #B3C0D1;
    color: #333;
    line-height: 60px;
  }
  
  .el-aside {
    color: #333;
  }
</style>

<script>
import Axios from "axios";

  export default {
    data() {
      const item = {
        date: '2016-05-02',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      };
      return {
        tableData: Array(20).fill(item)
      }
    },
    methods: {
    logout() {
      Axios.post("http://localhost:8080/logout").then(function(response) {
        if (response.data.code == 100) {
          sessionStorage.clear()
          alert(response.data.map.logout_msg);
          window.location.href = "http://localhost:8080"
        } else {
          alert(response.data.map.logout_msg);
        }
      });
    }
  }
  };
</script>
