<template>
  <div>
    <h1>新建书籍</h1>
    <el-form label-width="120px" @submit.native.prevent="addBook">
      <el-form-item label="书籍分类">
        <el-select v-model="book.bookTypeid">
          <el-option
            v-for="item of bookType"
            :key="item.typeId"
            :label="item.typeName"
            :value="item.typeId"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="书籍名称">
        <el-input type="text" v-model="book.bookName"></el-input>
      </el-form-item>
      <el-form-item label="数量">
        <el-input type="text" v-model="book.bookNumber"></el-input>
      </el-form-item>
      <el-form-item label="相关图片">
        <el-upload
          class="avatar-uploader"
          action="http://localhost:8080/Manager/upload"
          :show-file-list="false"
          :on-success="afterUpload1"
        >
          <img v-if="book.bookPhoto" :src="`http://localhost:8080${book.bookPhoto}`" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="作者">
        <el-input type="text" v-model="book.authorName"></el-input>
      </el-form-item>
      <el-form-item label="作者图片">
        <el-upload
          class="avatar-uploader"
          action="http://localhost:8080/Manager/upload"
          :show-file-list="false"
          :on-success="afterUpload2"
        >
          <img v-if="book.authorPhoto" :src="`http://localhost:8080${book.authorPhoto}`" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="作者介绍">
        <el-input type="textarea" v-model="book.authorInfo"></el-input>
      </el-form-item>
      <el-form-item label="出版社">
        <el-input type="text" v-model="book.publisher"></el-input>
      </el-form-item>
      <el-form-item label="出版日期">
        <el-date-picker v-model="book.publishtime" type="date" placeholder="选择日期"></el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      bookType: [],
      book: {}
    };
  },
  methods: {
    getBookType() {
      var that = this;
      axios
        .post("http://localhost:8080/Book/getBookType")
        .then(function(response) {
          that.bookType = response.data.map.booktype;
        });
    },
    addBook() {
      var that = this;
      axios
        .post(
          "http://localhost:8080/Manager/addBookAndAuthor",
          this.$qs.stringify(that.book)
        )
        .then(function(response) {
          if (response.data.code == 100) {
            that.$router.push("/book/list");
            that.$message({
              type: "success",
              message: "保存成功"
            });
          }
        });
    },
    afterUpload1(res) {
      this.$set(this.book,'bookPhoto',res.map.path)
    },
    afterUpload2(res) {
      this.$set(this.book,'authorPhoto',res.map.path)
    },
  },
  created() {
    this.getBookType();
  }
};
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>