import Vue from 'vue'
import VueRouter from 'vue-router'
import Main from '../views/Main.vue'
import BookEdit from '../views/BookEdit.vue'
import BookList from '../views/BookList.vue'
import BookUpdate from '../views/BookUpdate.vue'
import BorrowList from '../views/BorrowList.vue'

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'main',
    component: Main,
    children: [
      { path: '/book/edit', component: BookEdit },
      { path: '/book/update/:bookId', component: BookUpdate, props: true },
      { path: '/book/list', component: BookList },
      { path: '/borrow/list', component: BorrowList }
    ]
  },
]

const router = new VueRouter({
  routes
})

const originalPush = VueRouter.prototype.push
   VueRouter.prototype.push = function push(location) {
   return originalPush.call(this, location).catch(err => err)
}

export default router
